CACA,oCACA,YACA,CACA,oCACA,aACA,CACA,+BACA,oBACA,CAEA,oCACA,aACA,CACA,yCAEA,gBAAA,CADA,SAEA,CACA,8CACA,WACA,CACA,iDAIA,oBAAA,CADA,aAAA,CAFA,sBAAA,CAAA,iBAIA,CAEA,sCACA,SACA,CACA,2CAEA,YAAA,CACA,cAAA,CACA,sBACA,CACA,+CACA,cAAA,CACA,eACA,CAEA,mCACA,aAAA,CAEA,WAAA,CADA,SAEA,CACA,CAEA,yCACA,+DACA,wBACA,CACA,2CACA,mDACA,CACA,uCACA,SACA,CACA,mCACA,aAAA,CAEA,WAAA,CADA,SAEA,CACA","sourcesContent":["<template>\n    <div>\n        <div class=\"ven-land-main-cont\">\n            <section class=\"wed-ven-main\">\n                <div style=\"position: relative\">\n                    <img\n                        src=\"/assets/vendors/accomodations/mainBack.png\"\n                        alt=\"main_img\"\n                    />\n                    <div\n                        style=\"\n                            background: rgba(70, 25, 82, 0.6);\n                            width: 100%;\n                            height: 100%;\n                            position: absolute;\n                            top: 0%;\n                        \"\n                    ></div>\n                </div>\n                <div class=\"wed-ven-main-search\">\n                    <p>For a unique love story</p>\n                    <p>Find Beautiful & Affordable wedding Hotels In Town</p>\n                    <div>\n                        <form\n                            action=\"\"\n                            style=\"\n                                display: flex;\n                                justify-content: space-around;\n                                flex-wrap: wrap;\n                            \"\n                            @submit.prevent=\"searchAcc($event)\"\n                        >\n                            <!-- <select\n                                id=\"states\"\n                                name=\"states\"\n                                required\n                                v-model=\"search.city\"\n                            >\n                                <option hidden disabled selected value></option>\n                                <option\n                                    v-for=\"(value, index) in district\"\n                                    :key=\"index\"\n                                    :value=\"value.id\"\n                                >\n                                    {{ value.name }}\n                                </option>\n                            </select> -->\n\n                            <label style=\"width: 50%\" for=\"accstate\">\n                                <select\n                                    name=\"accstate\"\n                                    id=\"accstate\"\n                                    required\n                                    v-model=\"search.state\"\n                                    @change=\"getDistricts($event)\"\n                                >\n                                    <option disabled selected value=\"\">\n                                        Select States\n                                    </option>\n                                    <option\n                                        v-for=\"(item, index) in stateList\"\n                                        :key=\"index\"\n                                        :value=\"item\"\n                                    >\n                                        {{ item }}\n                                    </option>\n                                </select>\n                            </label>\n                            <label style=\"width: 50%\" for=\"accdistrict\">\n                                <select\n                                    name=\"accdistrict\"\n                                    id=\"accdistrict\"\n                                    required\n                                    v-model=\"search.city\"\n                                >\n                                    <option\n                                        v-if=\"hideDefaultOption\"\n                                        disabled\n                                        selected\n                                    >\n                                        Select State First\n                                    </option>\n                                    <option v-else disabled selected>\n                                        Select City\n                                    </option>\n\n                                    <option\n                                        v-for=\"(item, index) in district\"\n                                        :key=\"index\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                            </label>\n\n                            <div>\n                                <date-picker v-model=\"range\" is-range>\n                                    <template\n                                        v-slot=\"{ inputValue, inputEvents }\"\n                                    >\n                                        <div\n                                            class=\"flex justify-center items-center date-pick-input\"\n                                        >\n                                            <span>\n                                                <img\n                                                    src=\"/assets/vendors/accomodations/ant-design_calendar-outlined.svg\"\n                                                    alt=\"\"\n                                                    style=\"\n                                                        width: 18px;\n                                                        height: 18px;\n                                                        background-color: white;\n                                                        position: relative;\n                                                        top: -2px;\n                                                    \"\n                                                />\n                                            </span>\n                                            <input\n                                                :value=\"inputValue.start\"\n                                                v-on=\"inputEvents.start\"\n                                                class=\"checkin-out\"\n                                                type=\"text\"\n                                                placeholder=\"Check in\"\n                                                required\n                                            />\n                                            <span style=\"margin-left: -18px\"\n                                                >-</span\n                                            >\n                                            <input\n                                                :value=\"inputValue.end\"\n                                                v-on=\"inputEvents.start\"\n                                                class=\"checkin-out\"\n                                                type=\"text\"\n                                                required\n                                                placeholder=\"Check out\"\n                                            />\n                                        </div>\n                                    </template>\n                                </date-picker>\n                            </div>\n\n                            <div class=\"date-pick-input-dum\">\n                                <div\n                                    style=\"cursor: pointer\"\n                                    @click.stop=\"roomMember = !roomMember\"\n                                >\n                                    <span>\n                                        <img\n                                            src=\"/assets/vendors/accomodations/bi_person-fill.svg\"\n                                            alt=\"\"\n                                            style=\"\n                                                width: 18px;\n                                                height: 18px;\n                                                background-color: white;\n                                                position: relative;\n                                                top: -2px;\n                                            \"\n                                        />\n                                    </span>\n                                    <span style=\"width: 88%\"\n                                        >{{ search.adult }} Adults -\n                                        {{ search.children }} Children -\n                                        {{ search.rooms }} Rooms</span\n                                    >\n                                </div>\n                                <div\n                                    @click.stop\n                                    v-if=\"roomMember\"\n                                    class=\"roomMembers-feilds\"\n                                >\n                                    <div>\n                                        <span> Adult </span>\n                                        <span>\n                                            <button\n                                                @click=\"search.adult--\"\n                                                type=\"button\"\n                                            >\n                                                -\n                                            </button>\n                                            <input\n                                                type=\"number\"\n                                                v-model=\"search.adult\"\n                                            />\n                                            <button\n                                                @click=\"search.adult++\"\n                                                type=\"button\"\n                                            >\n                                                +\n                                            </button>\n                                        </span>\n                                    </div>\n                                    <div>\n                                        <span> Children </span>\n                                        <span>\n                                            <button\n                                                @click=\"search.children--\"\n                                                type=\"button\"\n                                            >\n                                                -\n                                            </button>\n                                            <input\n                                                type=\"number\"\n                                                v-model=\"search.children\"\n                                            />\n                                            <button\n                                                @click=\"search.children++\"\n                                                type=\"button\"\n                                            >\n                                                +\n                                            </button>\n                                        </span>\n                                    </div>\n                                    <div>\n                                        <span> Rooms </span>\n                                        <span>\n                                            <button\n                                                @click=\"search.rooms--\"\n                                                type=\"button\"\n                                            >\n                                                -\n                                            </button>\n                                            <input\n                                                type=\"number\"\n                                                v-model=\"search.rooms\"\n                                            />\n                                            <button\n                                                @click=\"search.rooms++\"\n                                                type=\"button\"\n                                            >\n                                                +\n                                            </button>\n                                        </span>\n                                    </div>\n                                </div>\n                            </div>\n                            <button type=\"submit\">Search</button>\n                        </form>\n                    </div>\n                </div>\n            </section>\n\n            <section class=\"wed-ven-featured container\">\n                <h1 style=\"margin-bottom: 2em\">Search by type of Hotels</h1>\n                <div>\n                    <a\n                        href=\"\"\n                        v-for=\"(item, index) in featuredCategory\"\n                        :key=\"index\"\n                    >\n                        <div class=\"wed-ven-featured-items\">\n                            <img\n                                src=\"/assets/vendors/landing/Frame911.png\"\n                                alt=\"\"\n                                width=\"100%\"\n                            />\n                            <div>\n                                <div class=\"wed-ven-subhead\">\n                                    {{ item.name }}\n                                </div>\n\n                                <div class=\"wed-ven-desc\">\n                                    <div>23 places found in chennai</div>\n                                </div>\n                            </div>\n                        </div>\n                    </a>\n                </div>\n            </section>\n\n            <section class=\"wed-ven-featured container\">\n                <h1 style=\"margin-bottom: 2em\">Popular searches</h1>\n                <div class=\"wed-venue-items\">\n                    <a\n                        href=\"\"\n                        v-for=\"(item, index) in popluarSearches\"\n                        :key=\"index\"\n                    >\n                        <div>\n                            <img\n                                src=\"/assets/venues/venueDefault.png\"\n                                alt=\"\"\n                                width=\"100px\"\n                                height=\"auto\"\n                            />\n                            <div class=\"wed-venue-items-desc\">\n                                <div>\n                                    <div>{{ item.name }}</div>\n                                    <div class=\"d-flex\">\n                                        <div>{{ item.rating }}</div>\n                                        <div>\n                                            <img\n                                                src=\"/assets/icon-SVG/Star.svg\"\n                                                alt=\"\"\n                                                style=\"margin-top: -5px\"\n                                            />\n                                        </div>\n                                    </div>\n                                </div>\n                                <div>\n                                    <p>{{ item.descp }}</p>\n                                    <p>{{ item.reviews }} reviews</p>\n                                </div>\n                            </div>\n                        </div>\n                        <div class=\"wed-venue-items-price\">\n                            <div>Starting from</div>\n                            <div>\n                                {{ item.priceStart }} <span>per day</span>\n                            </div>\n                        </div>\n                    </a>\n                </div>\n            </section>\n\n            <section class=\"wed-ven-testimonial\">\n                <div>\n                    <div class=\"testimonial-1\">Testimonials</div>\n                </div>\n\n                <div class=\"testimonials-cont\">\n                    <div>\n                        <div\n                            style=\"\n                                display: flex;\n                                justify-content: space-between;\n                            \"\n                        >\n                            <div style=\"flex: 1\">\n                                <div\n                                    class=\"sec7-arrow\"\n                                    style=\"\n                                        display: flex;\n                                        justify-content: flex-end;\n                                        height: 110px;\n                                    \"\n                                >\n                                    <div style=\"align-self: center\">\n                                        <button\n                                            style=\"\n                                                outline: none;\n                                                border: none;\n                                                background-color: inherit;\n                                            \"\n                                            id=\"prev-btn\"\n                                            @click=\"prevTestimonial()\"\n                                        >\n                                            <img\n                                                src=\"/assets/vendors/landing/right.png\"\n                                                alt=\"\"\n                                            />\n                                        </button>\n                                    </div>\n                                </div>\n                            </div>\n                            <div style=\"flex: 1\">\n                                <div\n                                    style=\"\n                                        display: flex;\n                                        justify-content: center;\n                                    \"\n                                >\n                                    <img\n                                        :src=\"\n                                            '/assets/testimonials/' +\n                                            showTestimonials().imageOne\n                                        \"\n                                        alt=\"\"\n                                        id=\"person-img\"\n                                        style=\"\n                                            width: 106px;\n                                            height: 110px;\n                                            border-radius: 50%;\n                                        \"\n                                    />\n                                </div>\n                            </div>\n                            <div style=\"flex: 1\">\n                                <div\n                                    class=\"sec7-arrow\"\n                                    style=\"\n                                        display: flex;\n                                        justify-content: flex-start;\n                                        height: 110px;\n                                    \"\n                                >\n                                    <div style=\"align-self: center\">\n                                        <button\n                                            style=\"\n                                                outline: none;\n                                                border: none;\n                                                background-color: inherit;\n                                            \"\n                                            id=\"next-btn\"\n                                            @click=\"nextTestimonial()\"\n                                        >\n                                            <img\n                                                src=\"/assets/vendors/landing/left.png\"\n                                                alt=\"\"\n                                            />\n                                        </button>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n\n                        <div class=\"row\">\n                            <div class=\"col\">\n                                <div class=\"testimonial-2\">\n                                    <span id=\"testimonial-name\">{{\n                                        showTestimonials().title\n                                    }}</span>\n                                    <!-- <span id=\"testimonial-prof\">{{\n                                        showTestimonials().profession\n                                    }}</span> -->\n                                </div>\n                            </div>\n                        </div>\n\n                        <div class=\"row\">\n                            <div class=\"col-10 col-md-5 m-auto\">\n                                <div\n                                    class=\"testimonial-3\"\n                                    v-html=\"showTestimonials().body\"\n                                ></div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </section>\n\n            <!-- <footer id=\"main-foot\" style=\"background: #461952\">\n                <div class=\"wed-host-footer\">\n                    <div>\n                        <div class=\"wed-host-footer-sec-1\">\n                            <img\n                                src=\"/assets/vendors/landing/logo.svg\"\n                                alt=\"\"\n                                srcset=\"\"\n                            />\n                            <div>\n                                Lorem ipsum dolor sit amet, consectetur\n                                adipiscing elit, sed do eiusmod tempor\n                                incididunt ut labore et dolore magna aliqua. Ut\n                                enim ad minim veniam, quis nostrud exercitation\n                                ullamco laboris nisi ut aliquip ex ea commodo\n                                consequat.\n                            </div>\n                        </div>\n                        <div class=\"wed-host-footer-sec-2\">\n                            <div>\n                                <h5>Services</h5>\n                                <a href=\"\">Terms of services</a>\n                                <a href=\"\">Custom field</a>\n                                <a href=\"\">Listing</a>\n                                <a href=\"\">Offline</a>\n                            </div>\n                            <div>\n                                <h5>About</h5>\n                                <a href=\"\">Our Story</a>\n                                <a href=\"\">Benefits</a>\n                                <a href=\"\">Team</a>\n                                <a href=\"\">Sitemap</a>\n                            </div>\n                            <div>\n                                <h5>Follow Us</h5>\n                                <a href=\"\"\n         